import { Directive, ElementRef, AfterViewInit } from '@angular/core';

declare var $:any

@Directive({
  selector: '[appUiDropdown]'
})

export class UiDropdownDirective implements AfterViewInit{

  constructor(private el: ElementRef) { }

  ngAfterViewInit() {
    $(this.el.nativeElement)
    .dropdown();
  }

}



