import { Component, OnInit, AfterViewInit } from '@angular/core';

import { ResumeService } from './resume.service';

declare var $: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit, AfterViewInit {
  resume: any;
  activatingProject: any;

  constructor(private resumeService: ResumeService) { }

  ngOnInit(): void {
    this.resume = this.resumeService.getResume()
  }

  ngAfterViewInit(): void {
    $('.ui.rating').rating('disable');
    $('.project.modal')
      .modal({ 
        observeChanges: true,
        onHidden: function() {
          this.activatingProject = undefined;
        }
      });

    
    $('.project.image').on('click', (e) => {
      let $this = this;
      
      let $modal = $('.project.modal');

      let $elem = $(e.currentTarget);
      let index = parseInt($elem.data('index'));
      
      $this.activatingProject = $this.resume.projects[index];

      $('.project.modal').modal('show');
    });
      
    $.ajax({
      url: "http://liuk-ai-mean-shopping.herokuapp.com/",
      dataType: "jsonp",
      beforeSend: function() {
        console.log("Waking up dyno...")
      },
      complete: function(data) {
        console.log("Dyno is awake now.");
      }
    });
  }

  // For loop
  dummy: Number[];
  createRange(start, end) {
    this.dummy = [];
    for (var i = start; i <= end; i++) {
      this.dummy.push(i);
    }
    return this.dummy;
  }
}



