import { Directive, ElementRef, AfterViewInit } from '@angular/core';

declare var $: any;

@Directive({
  selector: '[appUiCard]'
})
export class UiCardDirective implements AfterViewInit {

  constructor(private el: ElementRef) { }

  ngAfterViewInit() {
    $(this.el.nativeElement).dimmer({
      on: 'hover'
    });
  }

}



